.element__descr{
    width: calc(100% - 21px);
    max-width: 195px;
    background-color: #fff;
    color: #000;
    margin: 20px auto 32px 21px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    align-self: center;
}